<script>
import { defineComponent } from '@vue/composition-api'
import Chart from "@/components/Chart.vue"
import StockExchangePrices from "@/components/StockExchangePrices.vue"

import { MarketOverview } from 'vue-tradingview-widgets';
import { MarketData } from 'vue-tradingview-widgets';
import { ForexCrossRates } from 'vue-tradingview-widgets';
import { ForexHeatMap } from 'vue-tradingview-widgets';

import HeatMap from '../components/HeatMap.vue';


export default defineComponent({
    components: { Chart, StockExchangePrices, MarketOverview, MarketData, ForexCrossRates, ForexHeatMap, HeatMap },
    data: ()=>({
        width: 0,
        coins: [
            {
                coinName: 'OilGasCoalToken',
                coinTicker: 'OGCT'
            },
            {
                coinName: 'InternetCoin',
                coinTicker: 'ITC'
            },
            {
                coinName: 'GadgetCoin',
                coinTicker: 'GDC'
            },
            {
                coinName: 'CapitalWhalePresaleToken',
                coinTicker: 'CWPT'
            },
            {
                coinName: 'DOM',
                coinTicker: 'DOM'
            }
        ],
        showHeatMap: false,
        heatMapContainerStyle: null
    }),
    setup() {

    },
    computed: {
        marketOverviewLocale() {
            return this.$utils.getLangCodeFromLocale(this.$store.state.locale, false)
        }
    },

    methods: {
        openCoinPage(coinName){
            window.open(location.origin+'/#/coins/'+coinName, '_blank');
        },
        async buyCoin(coinTicker) {
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        }
    },

    // async loadData(){
        
    // },
    mounted(){
        this.width = document.getElementById('widthAnchor').offsetWidth;
        const heatMapWidth = this.width
        const heatMapHeight = this.width/2
        this.heatMapContainerStyle = `width: ${heatMapWidth}px; height: ${heatMapHeight}px; background: orange;`
        this.showHeatMap = false
        setTimeout(()=>{
            this.showHeatMap = true
        }, 0)
        // loadData();
        // let cryptorankScript = document.createElement('script')
        // cryptorankScript.setAttribute('src', 'https://cryptorank.io/widget/market-state.js')
        // document.head.appendChild(cryptorankScript)

        console.log(this.marketOverviewLocale)
    }
})
</script>

